<template>
	<view class="user">
		<view class="top">
			<view class="group" @click="toUserInfo">
				<view class="userinfo">
					<view class="pic">
						<!-- 自己登录的头像 -->
						<image v-if="hasLogin&&userInfo.avatar_file&&userInfo.avatar_file.url" :src="userInfo.avatar_file.url" mode="aspectFill"></image>
						<image v-else src="../../static/images/user-default.jpg" mode="aspectFill"></image>
					</view>
					<view class="text" v-if="hasLogin">
						<view class="nickname">{{userInfo.nickname||userInfo.username||userInfo.mobile}}</view>
						<view class="year">
							<uni-dateformat :date="userInfo.register_date" :threshold="[3600,99*365*24*60*60*1000]"></uni-dateformat>
							注册</view>
					</view>
					<view class="text" v-else>
						<view class="nickname">点击登录</view>
					</view>
					
				</view>
				
				<view class="more">
					<text class="iconfont icon-a-10-you"></text>
				</view>
			</view>
			
			<view class="bg">				
				<!-- 自己登录的头像背景 -->
				<image v-if="hasLogin&&userInfo.avatar_file&&userInfo.avatar_file.url" :src="userInfo.avatar_file.url" mode="aspectFill"></image>
				<image v-else src="../../static/images/user-default.jpg" mode="aspectFill"></image>
			</view>
		</view>
		
	
		
		<view class="main">
			<view class="info">
				<view class="item"><text>{{totalObj.likeNum}}</text>获赞</view>
				<view class="item"><text>无</text>评论</view>
				<view class="item"><text>{{totalObj.artNum}}</text>发帖</view>
			</view>
			
			<view class="list">
				<view class="group">					
					<view class="item" @click="goArticleList">
						<view class="left"><text class="iconfont icon-a-24-bianji"></text><text class="text">我的帖子</text></view>
						<view class="right"><text class="iconfont icon-a-10-you"></text></view>
					</view>
					<view class="item" @click="goLikeList">
						<view class="left"><text class="iconfont icon-a-106-xihuan"></text><text class="text">我的点赞</text></view>
						<view class="right"><text class="iconfont icon-a-10-you"></text></view>
					</view>
					<view class="item" @click="goComment">
						<view class="left"><text class="iconfont icon-a-21-xiugai"></text><text class="text">我的评论</text></view>
						<view class="right"><text class="iconfont icon-a-10-you"></text></view>
					</view>
				</view>
				
				<view class="group">
					<view class="item" @click="about">
						<view class="left"><text class="iconfont icon-a-32-wenjian"></text><text class="text">关于</text></view>
						<view class="right"><text class="iconfont icon-a-10-you"></text></view>
					</view>		
					<view class="item" @click="goFeedBack">
						<view class="left"><text class="iconfont icon-a-5-xinxi"></text><text class="text">意见反馈</text></view>
						<view class="right"><text class="iconfont icon-a-10-you"></text></view>
					</view>	
				</view>
				
				<view class="group">
					<view class="item" @click="logout">
						<view class="left"><text class="iconfont icon-a-73-tuichu"></text><text class="text">退出登录</text></view>
						<view class="right"><text class="iconfont icon-a-10-you"></text></view>
					</view>					
				</view>
			</view>
		</view>
		
		
		
		
	</view>
</template>

<script>
	import { store,mutations } from "@/uni_modules/uni-id-pages/common/store.js"
	const db= uniCloud.database()
	export default {
		data() {
			return {
				totalObj:{
					artNum:0, //文章数量
					likeNum:0//点赞数量
				}
			};
		},
		onLoad() {
			this.getTotal();
			
		},
		computed: {
			hasLogin() {
				return store.hasLogin
			},
			userInfo(){
				return store.userInfo
			}
		},
		methods:{
			async getTotal(){
				// 判断是否登录，如果登录则走下面逻辑，如果未登录则return不走
				if(!this.hasLogin) return
				// 发帖数
				let artCount = await db.collection("quanzi_article").where(`user_id == $cloudEnv_uid`).count()
				this.totalObj.artNum = artCount.result.total
				// 点赞数
				let likeCount = await db.collection("quanzi_article").where(`user_id == $cloudEnv_uid`)
				.groupBy("user_id").groupField("sum(like_count) as totalScore").get()
				// 判断
				if(this.totalObj.artNum){
					this.totalObj.likeNum = likeCount.result.data[0].totalScore
				}
				
				
				
			},
			// 点击跳转到意见反馈界面
			goFeedBack(){
				if(this.goLoginPage()){
					return true
				}
				uni.navigateTo({
					url:"/uni_modules/uni-feedback/pages/opendb-feedback/opendb-feedback"
				})
			},
			// 点击跳转到我的点赞界面
			goLikeList(){
				if(this.goLoginPage()){
					return true
				}
				uni.navigateTo({
					url:"/pages_self/quanzi_like/list"
				})
			},
			
			// 点击跳转到我的帖子界面
			goArticleList(){
				if(this.goLoginPage()){
					return true
				}
				uni.navigateTo({
					url:"/pages_self/quanzi_article/list"
				})
			},
			// 点击跳转到我的评论界面
			goComment(){
				if(this.goLoginPage()){
					return true
				}
				uni.showToast({
					title:"暂未开放！",
					icon:"none"
				})
			},
			// 点击跳转到关于界面
			about(){
				if(this.goLoginPage()){
					return true
				}
				uni.showToast({
					title:"暂未开放！",
					icon:"none"
				})
			},
			
			// 点击跳转到个人资料界面
			toUserInfo(){
				if(this.hasLogin){
					uni.navigateTo({
						url:"/uni_modules/uni-id-pages/pages/userinfo/userinfo"
					})
				}else{
					let route = this.$mp.page.route
					uni.navigateTo({
						url:"/uni_modules/uni-id-pages/pages/login/login-withpwd?uniIdRedirectUrl=/"+route
					})
				}
				
			},
			// 点击退出登录
			logout(){
				if(this.goLoginPage()){
					return true
				}
				uni.showModal({
					title:"是否退出登录?",
					success:res=>{
						if(res.confirm){
							mutations.logout()
						}
					}
				})
			},
			// 判断是否退出登录
			goLoginPage(){
				if(!this.hasLogin){
					uni.showToast({
						title:"您尚未登录，请登录！",
						icon:"none"
					})
					return true;
				}
				return false
			}
		}
	}
</script>

<style lang="scss">
.user{
	.top{
		height: 300rpx;
		background: #bbb;
		padding:0 30rpx;
		padding-top:var(--status-bar-height);
		position: relative;
		display: flex;
		align-items: center;
		.group{
			position: relative;
			z-index: 10;
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%;
			color:#fff;
			.userinfo{
				display: flex;
				width: 100%;				
				align-items: center;				
				.pic{
					width: 120rpx;
					height: 120rpx;
					border-radius: 50%;
					overflow: hidden;
					border:2px solid #fff;
					image{
						width: 100%;
						height: 100%;
					}
				}
				.text{
					padding-left:20rpx;
					.nickname{
						font-size: 44rpx;
						font-weight: 600;
					}
					.year{
						font-size: 26rpx;
						opacity: 0.6;
						padding-top:5rpx;
					}
				}
			}
			.more{
				.iconfont{
					font-size: 40rpx;
				}
			}
			
		}
		.bg{
			position: absolute;
			top: 0;
			left:0;
			width: 100%;
			height: 100%;
			overflow: hidden;
			image{
				width: 100%;
				height: 100%;
				filter: blur(10px);
				transform: scale(2);
				opacity: 0.5;
			}
		}
	}
	.main{
		width: 100%;
		min-height: 200rpx;
		background: #fff;
		border-radius: 30rpx;
		transform:translateY(-30rpx);
		padding:30rpx 0;
		.info{
			padding:10rpx 30rpx;
			display: flex;
			justify-content: space-between;
			font-size: 30rpx;
			border-bottom:10rpx solid #f8f8f8;
			.item{
				padding-right: 20rpx;
				color:#888;
				padding-bottom: 15rpx;
				text{
					display: flex;
					align-items: center;
					flex-direction: column;
					font-weight: 600;
					color:#333;
				}
			}
		}
		.list{
			.group{
				padding:15rpx 30rpx;
				border-bottom:12rpx solid #f8f8f8;
				.item{
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding:25rpx 0;
					font-size: 32rpx;
					color:#444;
					border-bottom:1rpx solid #f8f8f8;
					.left{
						display: flex;
						align-items: center;
						.iconfont{
							font-size: 38rpx;
							margin-right: 10rpx;
						}
					}
					.right{
						.iconfont{
							font-size: 26rpx;
						}
					}
				}
				.item:last-child{
					border: none;
				}
			}
			.group:last-child{
				border:none;
			}
		}
	}
	
}
</style>
